.search-input {
  padding: 10px 10px;
  height: 50px;
  position: relative;
}

.search-input::before {
  content: url('data:image/png;base64,');
  display: block;
  position: absolute;
  width: 15px;
  z-index: 3;
  height: 15px;
  font-size: 20px;
  top: 11px;
  left: 16px;
  line-height: 32px;
  opacity: 0.6;
}

.search-input > input {
  width: 240px;
  font-size: 16px;
  line-height: 22px;
  padding: 5px 10px 5px 25px;
  height: 32px;
  position: relative;
  background-color: #fefefe;
  border-radius: 5px;

}

.search-input > input:focus {
  outline: none;
}

.keyword-filter-container {
  padding-left: 15px;
}



